<template>
  <div>
    <span>code:{{code}}</span>
    <span>state:{{state}}</span>
  </div>
</template>

<script>
import SideNav from '@/components/sideNav'
import NavHeader from '@/components/header'
import weixinApi from '@/services/weixin'

var getQueryStrings = function() {
  var query_string = {};
  var query = window.location.href.split("?")[1];
  if (query) {
    var vars = query.split("&");
    for (var i = 0; i < vars.length; i++) {
      var pair = vars[i].split("=");
      // If first entry with this name
      if (typeof query_string[pair[0]] === "undefined") {
        query_string[pair[0]] = decodeURIComponent(pair[1]);
        // If second entry with this name
      } else if (typeof query_string[pair[0]] === "string") {
        var arr = [query_string[pair[0]], decodeURIComponent(pair[1])];
        query_string[pair[0]] = arr;
        // If third or later entry with this name
      } else {
        query_string[pair[0]].push(decodeURIComponent(pair[1]));
      }
    }
  }
  return query_string;
}

export default {
  components: {
    SideNav,
    NavHeader
  },
  mounted(){
    let params = getQueryStrings()
    this.code = params.code
    this.state = params.state
    weixinApi.getSignature().then(data=>{
      wx.config({
          debug: true, // ,apialertpclogpc
          appId: data.appId, // 
          timestamp: data.timestamp, // 
          nonceStr: data.nonceStr, // 
          signature: data.signature,// 
          jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'] // JS
      });
      wx.ready(function(){
        console.log('wx ready')
        let baseUrl = 'http://logic.dingnuo.ai/get-weixin-code.html?appid=wx493ee39e4edc9ddd&scope=snsapi_userinfo&redirect_uri='
        wx.onMenuShareTimeline({
          title: '', // 
          link: '', // JS
          imgUrl: '', // 
        });
        wx.onMenuShareAppMessage({
          title: '', // 
          desc: '', // 
          link: baseUrl+encodeURIComponent(location.origin+location.pathname), // JS
          imgUrl: '', // 
        });
      });
    })
  },
  data(){
    return {
      code:'',
      state:''
    }
  }
}

</script>

<style lang="scss">
</style>



