<template>
    <div class="">
        <div class="content">
            <div class="block " style="min-height: 400px">
                <div class="container">
                    <div class="row">
                        <div class="block-content " >
                            <a v-show="user.userId===''" class="btn btn-link btn-primary" @click="showChoose"></a>
                            <div v-show="user.userId!==''">
                                <img class="img-avatar" :src="user.headImgUrl"/>
                                <span class="">{{user.nickname}}</span>
                                <a v-show="!isEdit" class="btn-link " @click="showChoose"></a>
                            </div>
                            <hr>
                            <answer-metas :answer="answer" @on-data-change="refreshAnswer" rows="10"></answer-metas>
                        </div>
                    </div>

                    <div class="block-content ">

                    </div>
                    <div class="flex">
                        <a class="btn btn-primary btn-sm margin-right-15 dll-save" @click="save"></a>

                        <a class="btn btn-sm border dll-cancel" @click="back2question"></a>
                    </div>

                </div>
            </div>

        </div>

        <!--<answer-metas :answer="answer" @on-data-change="refreshAnswer" rows="10"></answer-metas>-->
        <answer-users title="" @on-choose="chooseAnswer" :show=showChooseModal @show="showChooseModal=true"
                      @hide="showChooseModal=false"></answer-users>
    </div>
</template>

<script>
    import AnswerUsers from "@/components/list-choose-user.vue";
    import questionApi from '@/services/question';
    import AnswerMetas from '@/components/list-grid-div-v2.vue';

    export default {
        components: {AnswerUsers, AnswerMetas},
        mounted() {
            let user;
            if(this.$route.query.user)
                user = JSON.parse(this.$route.query.user);
            this.answerId = this.$route.query.answerId ? this.$route.query.answerId : '';
            if (user) {
                this.isEdit = true;
                this.chooseAnswer(user);
                this.getUserAnswer();
            }
        },
        props: ['questionId'],
        data() {
            return {
                showChooseModal: false,
                answer: {
                    metas: [
//                        {
//                            type: 'TEXT',
//                            content: '\n' +
//                            'CRVSUV15%-20%25%15%2.5%10%\n' +
//                            '25%5%CBACCBCBB25%5%C13%B2.4%BA0.9%15%BA\n' +
//                            'ABBC'
//                        },
//                        {type: 'IMG', content: 'http://dingnuo-dev.oss-cn-hangzhou.aliyuncs.com/077d072b8dbd7928ebd226e2d95927e1tmp.jpeg',compressUrl:'123'},
//                        {type: 'TEXT', content: '1111111'},
//                        {type: 'TEXT', content: '222222'},

                    ],
                    showLink: false,
                    link: ""
                },

                answerMetas: [],
                userId: '',
                //
                answerId: '',
                //  
                user: {userNickName: '', userId: '', headImgUrl: ''},
                //
                isEdit: false
            }
        },
        methods: {
            chooseAnswer(user) {
                this.initAnswer();
                this.user = user;
                this.userId = user._id.$oid;
                if (this.questionId)
                    this.getUserAnswer()
            },
            getUserAnswer() {
                let vm = this;
                questionApi.getUserAnswer(vm.questionId, vm.userId).then(data => {
                    debugger
                    if (data)
                        vm.answer = data;
                    if (!vm.answer.metas)
                        this.$set(vm.answer, 'metas', []);
                    vm.answerId = data.answerId;
                })
            },
            refreshAnswer(data) {
                this.answer = data;
            },
            back2question() {
//                let name = window.localStorage.getItem('urlName');
//                let params = JSON.parse(window.localStorage.getItem('urlParams'));
                let returnPath = localStorage.getItem('returnPath');
                this.$router.push(returnPath)
            },
            save() {
                if (!this.userId) {
                    this.$message.error("");
                    return false;
                }
                if (!this.answer.metas || this.answer.metas.length === 0) {
                    this.$message.error("");
                    return false;
                }
                if (this.answerId)
                    this.editAnswer();
                else
                    this.addAnswer();

            },
            showChoose() {
                this.showChooseModal = true;
            },
            initAnswer() {
                this.answerMetas = [];
                this.userId = '';
                this.answerId = '';
            },
            editAnswer() {
                let vm = this;
                let an = {
                    answerId: vm.answerId,
                    metas: vm.answer.metas ? vm.answer.metas : [],
                    showLink: vm.answer.showLink ? vm.answer.showLink : false,
                    link: vm.answer.link ? vm.answer.link : ''
                };
                questionApi.editAnswer(an).then(data => {
                    vm.$message.success("")
//                    vm.initAnswer();
                    vm.back2question();
                });
            },

            addAnswer() {
                let vm = this;
                let an = {
                    questionId: vm.questionId,
                    userId: vm.userId,
                    metas: vm.answer.metas ? vm.answer.metas : [],
                    showLink: vm.answer.showLink ? vm.answer.showLink : false,
                    link: vm.answer.link ? vm.answer.link : ''
                };

                questionApi.addAnswer(an).then(data => {
                    vm.$message.success("")
//                    vm.initAnswer();
                    vm.back2question();
                });
            },


        }

    }

</script>

<style lang="scss">
    .flex {
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 50px;
    }


</style>


